:root {
  --heliotrope: #ca7df9;
  --white-smoke: #f4f4f4;
  --bright-pink-crayola: #f26a8d;
  --amaranth-pink: #f49cbb;
  --light-cyan: #cbeef3;
  --black: #000;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--white-smoke);
  flex-direction: column;
  align-items: center;
  font-family: Courier New, Courier, monospace;
  display: flex;
}

p, input, textarea, button {
  font-family: Courier New, Courier, monospace;
  font-size: medium;
  font-weight: 500;
}

h1 {
  text-align: center;
}

h5 {
  margin-top: .5rem;
}

ol {
  list-style: none;
}

button:hover {
  cursor: pointer;
}

.normal-text {
  font-weight: normal;
}

header {
  background-color: var(--heliotrope);
  color: #000;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header-container {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  display: flex;
}

.navigation-container {
  background-color: var(--white-smoke);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-height: 50px;
  display: flex;
}

.nav-link-button {
  color: var(--black);
  background-color: var(--white-smoke);
  text-align: center;
  cursor: pointer;
  border: 0;
  flex: 1;
  padding: 1rem 0;
  font-weight: 600;
}

.nav-link-button:hover {
  color: var(--bright-pink-crayola);
}

main {
  background-color: var(--amaranth-pink);
  flex-direction: column;
  width: 100%;
  display: flex;
}

.button {
  border: 0;
  border-radius: .5rem;
  margin-top: .5rem;
  padding: 1rem 1.5rem;
  font-weight: 600;
}

.button:hover {
  background-color: var(--heliotrope);
}

.add-modal {
  background-color: var(--amaranth-pink);
  flex-direction: column;
  align-items: start;
  gap: .5rem;
  width: 100%;
  margin: 0 auto;
  padding: 1rem;
  display: none;
}

.add-modal-div {
  background-color: var(--amaranth-pink);
  width: 100%;
  margin: 0 auto;
}

.recipe-label {
  font-weight: 600;
}

.recipe-input {
  background-color: var(--white-smoke);
  border: none;
  border-radius: .5rem;
  width: 100%;
  margin-top: .5rem;
  padding: .5rem;
}

.add-step-div, .add-tag-div {
  flex-direction: column;
  width: 100%;
  display: flex;
}

.steps-preview {
  margin-top: 1rem;
  margin-left: 2rem;
}

.tags-preview {
  padding-top: .5rem;
}

.recipe-steps-container, .recipe-tags-container {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.recipe-step-label, .recipe-tag-label {
  flex: 1;
  margin-right: 1rem;
}

.recipe-step-button, .recipe-tag-button {
  width: 35%;
  padding: 0;
}

.add-modal-button {
  min-width: 95px;
}

.add-modal-flex-end {
  justify-content: end;
  gap: 1rem;
  display: flex;
}

.recipe-favourite {
  flex: .1;
  padding: .5rem 0;
}

.tag-filter {
  align-items: center;
  gap: .5rem;
  height: 40px;
  margin: auto;
  padding-left: 1rem;
  display: flex;
  overflow-x: hidden;
}

.recipe-list {
  background-color: var(--white-smoke);
  width: 100%;
  padding: 1rem;
}

.recipe-card {
  margin: 1rem auto 0;
}

.recipe-list-h4 {
  padding-top: .5rem;
}

.recipe-tags {
  margin-bottom: .5rem;
  font-size: small;
}

.recipe-buttons {
  margin-bottom: .5rem;
}

.recipe-button {
  background-color: var(--amaranth-pink);
}

@media (width >= 768px) and (width <= 1024px) {
  .add-modal {
    flex-direction: column;
    align-items: start;
    gap: .5rem;
    max-width: 750px;
    margin: 0 auto;
    padding: 1rem;
  }

  .navigation-container {
    background-color: var(--white-smoke);
    justify-content: center;
    display: flex;
  }

  .nav-link-button {
    flex: 0;
    min-width: 125px;
  }

  .recipe-card {
    max-width: 750px;
  }

  .recipe-step-button, .recipe-tag-button {
    width: 25%;
    padding: 0;
  }
}

@media (width >= 1025px) {
  .navigation-container {
    background-color: var(--white-smoke);
    justify-content: center;
    display: flex;
  }

  .add-modal {
    width: 100%;
  }

  .add-modal-div {
    max-width: 750px;
  }

  .recipe-step-button, .recipe-tag-button {
    width: 20%;
  }

  .nav-link-button {
    flex: 0;
    min-width: 125px;
  }

  .recipe-card {
    max-width: 750px;
  }
}

#chatbot-container {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  padding: 10px;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  width: 100%;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  border: var(--bright-pink-crayola) 1px solid;
  border-radius: .5rem;
  width: 100%;
  margin-bottom: 10px;
  margin-right: 5px;
  padding: 5px;
}

.history {
  padding-bottom: 15px;
}

.chatbot-buttons {
  justify-content: center;
  align-items: center;
  gap: .5rem;
  width: 100%;
  display: flex;
}

#send-btn {
  width: 90%;
  display: none;
}

#close-chat-btn {
  width: 90%;
}

.chat-button {
  color: var(--black);
  background-color: var(--white-smoke);
  text-align: center;
  cursor: pointer;
  border: 0;
  flex: 1;
  font-family: Courier New, Courier, monospace;
  font-size: medium;
  font-weight: 500;
}

.remove-tag {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin-left: 10px;
  font-size: 12px;
}
